@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');

:root {
  --background: #FCF8EC;
  --wrapper-bg: #D0E8F2;
  --button-color: #79A3B1;
  --text-primary: #222222;
  --text-secondary: #222222;  
  --border: #FCF8EC;
  --btn-border: black;  
}

[data-theme='dark'] {
  --background: #1F1D36;
  --wrapper-bg: #3F3351;
  --button-color: #E9A6A6;
  --text-primary: #F0F0F0;
  --text-secondary: #222222;  
  --border: #1F1D36;
  --btn-border: white;   
}

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding:0;  
  font-family: 'Ubuntu', sans-serif;
}

.app {  
  background-color: var(--background);
  color: var(--text-primary);
  padding-top: 20px;
  transition: ease-in-out 0.4s;
}

.Wrapper {
  margin: auto;
  width: 40%;
  padding: 10px 100px 10px 100px;  
  border-radius: 15px;
  opacity: 70%;
  background-color: var(--wrapper-bg);  
}

h1 {
  text-align: center;
}

h2 {
  text-align: center;
}

.theme-toggle h1 {
  font-size: 2rem;
  cursor: pointer;
}